.admin-form {
    width: 30vw;  /* 너비를 400px로 설정하여 LoginForm과 JoinForm에 맞춤 */
    display: flex;
    flex-direction: column;
    margin: -4vh auto;
    padding: 3.5vh 3.1vw;  /* 패딩 조정 */
    box-sizing: border-box;  /* 박스 모델 설정 */
    background-color: #f2efee;  /* 배경색 설정 */
    border-radius: 11px;  /* 모서리 둥글게 */
    box-shadow: 0 2.4rem 4.8rem rgba(0, 0, 0, 0.15);  /* 그림자 설정 */
}

.admin-title {
    padding: 2.3vh 1.8vw;  /* 패딩은 유지 */
    font-size: 22px;  /* 글자 크기 설정 */
    font-weight: 600;  /* 글자 두께 설정 */
    text-align: center;  /* 텍스트 중앙 정렬 */
    margin: 0;  /* 마진을 0으로 설정하여 여백 제거 */
}

.admin-form button {
    height: 5.9vh;
    align-self: end;
    padding: 1.4vh 1.3vw;
}

.readonly-input {
    background-color: #dfdfdf !important; /* 연한 회색 배경 */
    border: 1px solid #ccc !important; /* 연한 회색 테두리 */
    color: #5b5b5b !important; /* 텍스트 색상 */
    cursor: not-allowed !important; /* 커서 모양 변경 */
}

.readonly-input:focus {
    outline: none !important; /* 포커스 시 아웃라인 제거 */
    border: 1px solid #ccc !important; /* 포커스 시 테두리 제거 */
    box-shadow: none !important; /* 포커스 시 그림자 제거 */
    transform: none !important; /* 포커스 시 변형 제거 */
}